'use client'

import { Priority } from "@/shared/types/priority.types"
import { Status } from "@/shared/types/status.types"
import { User } from "@/shared/types/user.types"

interface SelectProps {
    label: string,
    options: any[] | User[] | Status[] | Priority[],
}

export function Select({label, options}: SelectProps) {
    return (
        <label className="flex flex-col gap-[4px]">
            <span className="font-light text-sm text-gray-400">{label}</span>
            <select className="bg-white w-60 h-10 rounded text-sm text-black-300 font-normal">
                {/* {options.map((item, index) => (
                    <option key={index} value={item}>
                        {item}
                    </option>
                ))} */}
                {options.map((item, index) => (
                    <option key={index} value={item}>
                        {item}
                    </option>
                ))}
            </select>
        </label>
    )
}
