import { fetchConfig } from "@/api/fetchConfig"
import { useGetAllUsers } from "@/hooks/useGetAllUsers"
import { api } from "@/pages/api/api"
import { APICOMMAND } from "@/shared/types/command.types"
import { Task } from "@/shared/types/task.types"
import { User } from "@/shared/types/user.types"
import { useEffect, useState } from "react"

interface Props {
    data: Task
}

export function TabTasksInProcess({data}: Props) {
    
    const author = useGetAllUsers(data)

    return (
        <>
            <td className="border-r-1 border-gray-300 p-2">
                {data.priorityid.namepriority}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.statusid.namestatus}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.nametask}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.info}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.datecreate}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.dateend}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.datechange}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {data.document}
            </td>
            <td className="border-r-1 border-gray-300 p-2">
                {`${author?.firstname} ${author?.lastname}`}
            </td>
        </>
    )
}
